import './AboutMetals.css';
import TransitionGraph from '../TransitionGraph/TransitionGraph'

export default function AboutMetals() {
    return(
        <>
        <section className='metals'>
        <div className='metals__container'>
            <h1 className='metals__title'>Pourquoi les mtaux critiques ?</h1>
            <div className='metals__description'>
            <p className='metals__paragraph'>
            Ces mtaux constituent la base des transitions numrique et nergtique.
            </p>
            <p className='metals__paragraph'>
            La scurisation de ces mtaux reprsente un enjeu de souverainet majeur ainsi quune opportunit dpargne exceptionnelle pour les 30 ans  venir.
            </p>
            {/* <p className='metals__paragraph'>
            Les prix des metaux vont augmenter au cours des 20 prochaines annes
            </p> */}
            </div>
            </div>
            <TransitionGraph className='metals__graphics'/>
        </section>
        </>
    )
}
